<template>
  <div class="accountManage">
    <div class="record-bar">
      <van-nav-bar
        title="银联转账"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="tool payTool">
      <van-cell title="收款人" :value="to_user_name" center/>
      <van-cell title="收款银行" :value="to_bank_name" center/>
      <van-cell title="收款卡号" :value="to_user_card" center/>
      <van-cell title="支付过期时间" :value="gqtime" center/>
      <van-cell title="付款金额" :value="money" center/>
      <van-cell title="系统订单号" :value="orderid" center/>
      <van-cell title="商户订单号" :value="tradeno" center/>
    </div>
    <div class="withdrawal-but" @click="ConfirmRecharge">返回</div>
    <router-view></router-view>
  </div>
</template>

<script>
  import EP from '../../../utils/encryption'
  import { Toast } from 'vant'
  // plus.navigator.setStatusBarStyle('dark');
  export default {
    name: 'myNews',
    data () {
      return {
        to_bank_name:'',
        to_user_name:'',
        to_user_card:'',
        gqtime:'',
        money:'',
        orderid:'',
        tradeno:'',
      }
    },
    created(){
      let that = this
      console.log(this.$route.params.data)
      let routeData = this.$route.params.data
      that.to_bank_name = routeData.to_bank_name
      that.to_user_name = routeData.to_user_name
      that.to_user_card = routeData.to_user_card
      that.money = routeData.money
      that.orderid = routeData.orderid
      that.tradeno = routeData.tradeno
      let data = routeData.gqtime*1000
      let thisTowDate = new Date(data);
      let currentTowTime = thisTowDate.getFullYear()+'年'+(thisTowDate.getMonth() + 1).toString().padStart(2,'0')+'月'+(thisTowDate.getDate()).toString().padStart(2,'0')+'日 '+(thisTowDate.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate.getMinutes()).toString().padStart(2,'0');
      that.gqtime = currentTowTime
      that.global.ws.onmessage = function(res) {
        that.websocketonmessage (res)
      };
    },
    methods: {
      websocketonmessage (e) { // 数据接收
        this.redata = JSON.parse(e.data)
        let that = this
        console.log(this.redata)
        if(this.redata.action === 'notifimoneychange'){
          this.$router.push({
            name:'replaceBC'
          })
        }
      },
      linkHome(){
        this.$router.push({
          name:'rechargeState'
        })
      },
      ConfirmRecharge(){
        this.$router.push({
          name:'rechargeState'
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/my";
  .value-class{
    color: #EC4138!important;
  }
  .not-value-class{
    color: #222!important;
  }
</style>



